const PACKAGE_NAME = '@wix/auto_sdk_events_forms';

/** Retrieves an event registration form. */
export function getForm(payload: object): RequestOptionsFactory<any> {
  function __getForm({ host }: any) {
    const metadata = {
      entityFqdn: 'wix.events.v1.form',
      method: 'GET' as any,
      methodFqn: 'wix.events.form.FormBuilder.GetForm',
      packageName: PACKAGE_NAME,
      migrationOptions: {
        optInTransformResponse: true,
      },
      url: resolveWixEventsFormFormBuilderUrl({
        protoPath: '/v1/events/{eventId}/form',
        data: payload,
        host,
      }),
      params: toURLSearchParams(payload),
    };

    return metadata;
  }

  return __getForm;
}

