/**
 * Updates a set of defined form messages that are displayed to a site visitor before, during, and after the registration flow.
 * Includes the configuration of form titles, response labels, "thank you" messages, and call-to-action texts.
 */
export function updateMessages(payload: object): RequestOptionsFactory<any> {
  function __updateMessages({ host }: any) {
    const serializedData = transformPaths(payload, [
      {
        transformFn: transformSDKFieldMaskToRESTFieldMask,
        paths: [{ path: 'fields' }],
      },
    ]);
    const metadata = {
      entityFqdn: 'wix.events.v1.form',
      method: 'PATCH' as any,
      methodFqn: 'wix.events.form.FormBuilder.UpdateMessages',
      packageName: PACKAGE_NAME,
      migrationOptions: {
        optInTransformResponse: true,
      },
      url: resolveWixEventsFormFormBuilderUrl({
        protoPath: '/v1/events/{eventId}/form/messages',
        data: serializedData,
        host,
      }),
      data: serializedData,
    };

    return metadata;
  }

  return __updateMessages;
}

